/* git */
@-keyframes grow-50{
  from { left:100%; }
  to { left:50%; }
}

@-webkit-keyframes grow-50{
  from { left:100%; }
  to { left:50%; }
}

/* axios, mongoose, mongoDB, express */
@-keyframes grow-60{
  from { left:100%; }
  to { left:40%; }
}

@-webkit-keyframes grow-60{
  from { left:100%; }
  to { left:40%; }
}

/* node */
@-keyframes grow-70{
  from { left:100%; }
  to { left:30%; }
}

@-webkit-keyframes grow-70{
  from { left:100%; }
  to { left:30%; }
}

/* javascript, redux */
@-keyframes grow-85{
  from { left:100%; }
  to { left:15%; }
}

@-webkit-keyframes grow-85{
  from { left:100%; }
  to { left:15%; }
}

/* css, html, react */
@-keyframes grow-90{
  from { left:100%; }
  to { left:10%; }
}

@-webkit-keyframes grow-90{
  from { left:100%; }
  to { left:10%; }
}

