  justify-content:center;
  align-items:center;
}

.stats-bars {
  width:100%;
  height:28px;
  position:relative;
}

.stats-bars .inner-bar {
  position: absolute;
  bottom: 0;
  right: 0;
  top: 0;
  background-color:rgb(31, 125, 247);
  transition: all 200ms cubic-bezier(0.15, 2, 0.5, 1.275);
  display:flex;
  justify-content:flex-end;
  padding-right:12px;
  align-items:center;
  color:white;
  border-bottom:1px solid white;
}

.stats-bars.inner-bar:hover {
  color:white;
  opacity:1;
}

.stats-bars.pct-50 .inner-bar  {  
  -webkit-animation-duration: .5s;
  -webkit-animation-name: grow-50;
  left:50%;
}

.stats-bars.pct-60 .inner-bar {
  -webkit-animation-duration: 1s;
  -webkit-animation-name: grow-60;
  left:40%;
}

.stats-bars.pct-70 .inner-bar {
  -webkit-animation-duration: 1.5s;
  -webkit-animation-name: grow-70;
  left:30%;
}

.stats-bars.pct-85 .inner-bar {
  -webkit-animation-duration:1s;
  -webkit-animation-name: grow-85;
  left:15%;
}

.stats-bars.pct-90 .inner-bar {
  -webkit-animation-duration:2s;
  -webkit-animation-name: grow-90;
  left:10%;
}

